'use client'
import {Box, Button, Checkbox, FormControlLabel, FormGroup, Stack, Typography} from "@mui/material";
import EnhancedTable from "@/components/table";
import handleSendToApiBackend from "@/api/handle-send-to-api";
import * as React from "react";
import JSONRows from "@/JSON/data-ru";


export default function Home() {
    const [selected, setSelected] = React.useState<readonly number[]>([]);

    const handleSendToApi=async (selected) => {
        console.log(selected)
        await handleSendToApiBackend(selected)
    }
    const showOnlySelected =()=>{
        const filteredData = JSONRows.filter(item => selected.includes(item.id));
        setRowsFofShow(filteredData);
    }
    const  [rows, setRowsFofShow] = React.useState(JSONRows);

  return (
      <Stack spacing={3}>
          <Box sx={{ marginTop: 3, marginX: "auto" }}>
              <Typography variant="h3" sx={{textAlign: "center", textTransform: "uppercase"}}>
                  Список стран и используемых в них валют
              </Typography>
          </Box>
          <Stack direction="row" spacing={2} flex justifyContent="space-around">
              <Button sx={{width: 160}}  size="medium" variant="contained" onClick={()=>showOnlySelected()}>Выбранное</Button>
              <Button sx={{width: 160}}  size="medium" variant="contained">
                  страна+валюты
              </Button>
              <Button sx={{width: 160}}  size="medium" variant="contained" >
                  валюта+страны
              </Button>
          </Stack>
          <Box>
              <Box>
                  <EnhancedTable handleSendToApi={handleSendToApi} rows={rows} />
              </Box>
          </Box>
      </Stack>
  );
}
